.main {
    background-color: #d19ee0b2;
    margin: auto;
    padding: 3rem 10rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 30%;
    color: #eee;
    box-shadow: 0 0 50px 2px rgba(238, 238, 238, 0.5);
}

.form {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title {
    font-size: 3rem;
    font-weight: 700;
    margin-bottom: 2rem;
}

.input {
    border: 3px solid #eee;
    background-color: transparent;
    border-radius: 3rem;
    padding: 1rem 2rem;
    font-size: 1.6rem;
    letter-spacing: 1.2px;
    color: #eee;
    display: block;
    margin-top: 2rem;
    width: 100%;
    font-weight: 600;
}

.input::placeholder {
    color: rgba(238, 238, 238, 0.5);
}

.login__btn {
    display: inline-block;
    margin-top: 5rem;
    width: 50%;
    height: 5rem;
    font-size: 1.8rem;
    font-weight: 600;
    border-radius: 200px;
    cursor: pointer;
    color: var(--primary-dark);
    transition: .2s linear;

}

.login__btn:hover {
    background-color: var(--primary-dark);
    color: #eee;
    box-shadow: 0 0 10px 2px #eee;
}