.main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2vh 10vw;
    background-color: var(--primary);
    box-shadow: 0 0 20px 0 var(--primary);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 99;
}

.logo {}

.logo__img {
    width: 200px;
    -webkit-user-drag: none;
}

.nav {
    display: flex;
}

.nav__item {
    margin: 0 5rem;
}

.nav__link {
    font-size: 2rem;
    font-weight: 600;
    color: #fff;
    display: block;
    padding: 1rem;
    position: relative;
    transition: .2s linear;
}

.nav__link::after {
    content: '';
    display: block;
    position: absolute;
    height: 2px;
    width: 100%;
    background-color: var(--primary-dark);
    bottom: 0;
    left: 0;
    border-radius: 3px;
    transform: scaleX(0);
    transition: .2s linear;
}

.nav__link:hover::after {
    transform: scaleX(1);
}

.nav__link:hover {
    color: var(--primary-dark);
}

.user {
    background-color: var(--primary-dark);
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    box-shadow: 0 0 10px 2px var(--primary);
    transition: .2s linear;
    position: relative;
}

.user__icon {
    font-size: 2rem;
    color: #fff;
    transition: .2s linear;
}

.user:hover {
    box-shadow: 0 0 20px 10px var(--primary-light);
}

.user:hover .user__icon {
    transform: scale(1.1);
}

.user__control {
    position: absolute;
    top: calc(100% + 2rem);
    background-color: var(--primary-light);
    width: 180px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-weight: 600;
    left: 50%;
    border-radius: 10px;
    box-shadow: 0 0 10px 0 var(--primary-dark);
    transform-origin: 50% -10%;
    transform: translateX(-50%) scale(0);
    transition: all .1s ease-in;
    z-index: -1;
}

.user:hover .user__control {
    transform: translateX(-50%) scale(1);
}

.user__control::after {
    content: '';
    display: block;
    width: 100%;
    position: absolute;
    height: 50px;
    bottom: 100%;
    z-index: -1;
}

.user__control::before {
    content: '';
    display: block;
    border: 10px solid;
    border-color: transparent transparent var(--primary-light) transparent;
    position: absolute;
    bottom: 100%;
    left: 50%;
    filter: drop-shadow(0 -3px 1.5px var(--primary-dark));
    transform: translateX(-50%);
}

.logout {
    color: var(--primary-dark);
    font-weight: 600;
    border-bottom: 3px solid var(--primary-dark);
}