/* ================================ IMPORT FONT ================================ */
@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* ================================ GENERAL CSS ================================ */
:root {
  --primary: #F190B7;
  --primary-bold: #CE49BF;
  --primary-dark: #A63EC5;
  --primary-light: #FBD6D2;
  --text: #333;
}

* {
  font-family: 'Quicksand', serif;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  will-change: auto;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input,
button {
  border: none;
  outline: none;
}

html {
  scroll-behavior: smooth;
  font-size: 10px;
}