.main {
    width: 65%;
    padding: 5rem;
}

.heart {
    font-size: 2.5rem;
    cursor: pointer;
    position: absolute;
    top: 4rem;
    right: 1rem;
}


.member {
    position: relative;
    display: flex;
    align-items: center;
    padding-right: 10rem;
}

.member+.member {
    border-top: 1px solid var(--primary-dark);
    margin-top: 32px;
    padding-top: 32px;
}

.member__avt {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 2px solid var(--primary);
    -webkit-user-drag: none;
}

.info {
    font-size: 1.8rem;
    margin-left: 2rem;
    display: flex;
    flex-direction: column;
    flex: 1;
}

.info__title {
    color: var(--primary-dark);
    font-weight: 700;
    font-size: 3rem;
    position: relative;
    width: max-content;
}


.info__title::after {
    content: '';
    display: block;
    width: 100%;
    height: 2px;
    border-radius: 3px;
    position: absolute;
    top: 103%;
    left: 0;
    background-color: var(--primary);
}

.info__desc {
    font-size: 1.8rem;
    font-weight: 500;
    color: var(--text);
    margin-top: 16px;
}

.process__wrap {
    width: 100%;
    background-color: var(--primary-dark);
    border-radius: 10px;
    margin-top: 32px;
    display: flex;
    padding: 3rem;
    box-shadow: 0 20px 20px -10px rgba(0, 0, 0, 0.6),
        0 -10px 10px -8px rgba(0, 0, 0, 0.3),
        inset 0 -20px 10px 0 rgba(0, 0, 0, 0.5),
        inset 0 5px 10px 5px var(--primary);
}

.process {
    width: 100%;
    height: 2rem;
    box-shadow:
        inset 0 10px 10px -5px rgba(0, 0, 0, 0.5),
        inset 0 -5px 20px 5px rgba(0, 0, 0, 0.5);
    border-radius: 3rem;
}

.process__inner {
    width: 50%;
    height: 100%;
    background: linear-gradient(rgb(255, 167, 5) 40%, rgb(255, 136, 0) 80%);
    border-radius: 3rem;
    box-shadow: 0 3px 5px 0px rgba(0, 0, 0, 0.5),
        5px 0 5px 0px rgba(0, 0, 0, 0.5),
        inset 0 -2px 3px 0 rgba(0, 0, 0, 0.5);
    position: relative;
    transition: 1s ease-in-out;
}

.process__inner:hover {
    width: 80%;
}

.process__param {
    width: 50px;
    height: 30px;
    position: absolute;
    background-color: rgb(255, 167, 5);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    font-size: 1.6rem;
    font-weight: 700;
    right: 0;
    bottom: calc(100% + 20px);
    transform: translateX(50%);
    color: var(--primary-dark);
}

.process__param::after {
    content: '';
    display: block;
    border: 10px solid;
    border-color: rgb(255, 167, 5) transparent transparent transparent;
    position: absolute;
    top: 100%;
}