.main {
    background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url(../../Assets/Images/login/login_bg.jpg);
    width: 100%;
    height: 100vh;
    background-size: cover;
    display: flex;
    position: relative;
}

.logo {
    width: 200px;
    object-fit: contain;
    -webkit-user-drag: none;
    position: absolute;
    top: 3em;
    left: 3em;
}