.main {
    flex: 1;
    padding: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: min-content;
    position: sticky;
    top: 10rem;
}

.logo {
    user-select: none;
    width: 80%;
    -webkit-user-drag: none;
}

.top__list {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin-top: 32px;
}

.top__item {
    display: flex;
    align-items: center;
    margin-top: 2rem;
}


.item__img {
    width: 50px;
    filter: drop-shadow(0 5px 2px rgba(0, 0, 0, .5));
    -webkit-user-drag: none;
    user-select: none;
}

.item__name {
    font-size: 2.2rem;
    color: #353535;
    line-height: 2;
    font-weight: 700;
    border-bottom: 2px solid var(--primary);
    margin-left: 16px;
    width: 100%;
}